.home-section-2 {
	position: relative;
	overflow: hidden;
	padding: 107px 0 123px;

	&::before {
		content: '';
		position: absolute;
		top: -490px;
		left: -340px;
		width: 721px;
		height: 1247px;
		background-image: url(../img/bg-before.png);
	}

	&::after {
		content: '';
		position: absolute;
		bottom: -325px;
		right: 0;
		width: 678px;
		height: 989px;
		background-image: url(../img/bg-before.png);
		object-fit: cover;
	}

	.title-about {
		h5 {
			display: inline-block;
			font-size: 46px;
			color: #000000;
			font-weight: 900;
			text-decoration: underline;
		}

		h3 {
			font-size: 72px;
			color: #ED1D24;
			font-weight: 900;
		}

		p {
			font-size: 24px;
			color: #000000;
			margin-bottom: 10px;

			span {
				color: #ED1D24;
				font-weight: 900;
			}
		}

		.level-1 {
			font-weight: 700;
			font-size: 30px;
		}

		.level-2 {
			font-size: 24px;
			font-weight: 700;
			margin-left: 25px;
		}

		.level-3 {
			font-size: 20px;
			margin-left: 25px;
		}

		.level-4 {
			font-size: 18px;
			margin-left: 50px;
		}
	}



	.box-guide {
		position: relative;
		margin-top: 43px;
		padding: 65px 90px;
		background: #000;
		border: 10px solid #ED1D24;
		width: 100%;
		box-shadow: 0 0 7px rgba(0, 0, 0, 0.5);

		.title-guide {
			h3 {
				font-size: 60px;
				font-weight: 800;
				color: #ED1D24;
				text-align: center;
			}
		}

		.desciption-guide {
			font-size: 20px;

			h5.step {
				font-weight: 800;
				color: #ED1D24;
				text-decoration: underline;
				margin-bottom: 15px;
			}

			p {
				color: #FFFFFF;
				margin-bottom: 24px;
			}

			h3 {
				color: #fff;
				font-weight: 800;
				margin-bottom: 40px;
			}

			span {
				display: inline-block;
				color: #fff;
				font-style: italic;
				margin-bottom: 20px;
			}

			.popupink {
				display: inline-block;
				color: #ED1D24;
				cursor: pointer;
			}

			img {
				width: 280px;
				margin: 0 auto;
				display: block;
			}

			.submit {
				display: block;
				margin: 100px auto 0;
				border: none;
				line-height: 95px;
				background: #ED1D24;
				padding: 0 85px;
				font-size: 34px;
				font-weight: 800;
				color: #ffF;
				text-align: center;
			}
		}

		&::after {
			content: '';
			position: absolute;
			display: block;
			background-image: url(../../../img/home/bg-box-guide.png);
			width: 761px;
			height: 381px;
			bottom: 180px;
			left: 50%;
			transform: translateX(-50%);
			pointer-events: none;
		}
	}
}

@media(max-width: 1024px) {
	.home-section-2 {
		&::after {
			display: none;
		}

		&::before {
			display: none;
		}

		padding: 20px;

		.title-about {
			h5 {
				font-size: 18px;
			}

			h3 {
				font-size: 24px;
				margin-bottom: 38px;
			}

			p {
				font-size: 16px;
			}
		}

		.box-guide {
			padding: 25px;

			.title-guide {
				h3 {
					font-size: 24px;
				}
			}

			.desciption-guide {
				h5.step {
					font-size: 18px;
					margin-bottom: 5px;
				}

				p {
					font-size: 16px;
				}

				h3 {
					font-size: 16PX;
					margin-bottom: 30px;
				}

				span {
					font-size: 16px;
				}

				.submit {
					margin: 35px auto 0;
					font-size: 16px;
					line-height: 45px;
					width: 50%;
				}
			}
		}
	}
}

@media(max-width: 768px) {
	.home-section-2 {
		.box-guide {
			.desciption-guide {
				.submit {
					width: 100%;
					padding: 0;
				}
			}
		}
	}
}